
body {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: #000;
}
li {
  list-style: none;
}
input {
  outline: none;
}

.loginMain {
  width: 100vw;
  height: 100vh;
  background: -webkit-linear-gradient(45deg, skyblue, pink);
  overflow: hidden;
}
.loginMain .title {
  color: #fff;
  font-size: 32px;
  font-weight: 700;
  text-align: center;
  cursor: default;
}
.loginMain input {
  display: block;
  width: 240px;
  height: 36px;
  padding: 0px 10px;
  border: 0px;
  border-radius: 5px;
  margin: 20px auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 16px;
  color: #fff;
  background-color: rgba(80, 80, 80, 0.3);
}
.loginMain input:focus {
  -webkit-box-shadow: 0 2px 3px 0 rgb(0 0 0 / 10%) inset, 0 2px 7px 0 rgb(0 0 0 / 20%);
          box-shadow: 0 2px 3px 0 rgb(0 0 0 / 10%) inset, 0 2px 7px 0 rgb(0 0 0 / 20%);
}
.loginMain button {
  display: block;
  width: 240px;
  height: 36px;
  padding: 0px 10px;
  border: 0px;
  border-radius: 5px;
  margin: 20px auto;
  font-size: 16px;
  color: #eee;
  background-color: #ff8656;
  outline: none;
  cursor: pointer;
}
.loginMain button:hover {
  background-color: #f05418;
}
.loginMain a {
  display: inline-block;
  font-size: 12px;
  cursor: pointer;
  color: #555555;
}
.loginMain a:hover {
  color: #000;
}

.loginBox {
  margin: 160px auto;
  width: 360px;
  text-align: center;
}






























































